<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-spacer></v-spacer>
        
        <v-card class="ma-1" cols="12">
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h5> Add a store owner </h5>
                <v-select
                label="OWNERS"
                outlined
                dense
                v-model="unique_id"
                :items="items"
                ></v-select>
                    <h6>This field is required.</h6>
                </v-col>
            </v-row>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2 primary"
            light
            :loading="loading"
            @click="add_owner"
            >
            ADD OWNER
            </v-btn>
            </v-col>
        </v-container>
        </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import StoreService from '@/services/StoreService';
import UserService from '@/services/UserService';
import Swal from 'sweetalert2';

export default {
    components: {
      NavBar
    },

    data: () => ({
        title: 'ADD OWNER',
        loading: false,
        items: [],
        unique_id:''
    }),

    computed:{
        idStore(){
            return this.$route.params.id
        }
    },

    methods:{
        add_owner(){
            this.loading = true
            StoreService
                .add_owner(this.unique_id, this.idStore)
                .then(res => {
                    Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: res['message']
                    }).then(() => {
                        this.loading = false
                        this.$router.go(-1)
                    })
                })
                .catch(e => {
                        /* 
                        *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                        */
                    let error = e.response.data;
                    let msg = ''
                    Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                    });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
        },

        list_admin(){
            UserService
            .list_admin()
            .then((res) => {
                let elements = res.filter(item => !item.is_staff);
                elements.forEach(item => {
                    this.items.push({ text: item.username , value: item.unique_id })
                })
            })
            .catch(e => {
                        /* 
                        *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                        */
                    let error = e.response.data;
                    let msg = ''
                    Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                    });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
        }

    },

    mounted(){
        this.list_admin()
    }
}
</script>